@mixin pseudo {
  content: "";
  position: absolute;
}

@mixin line($bottom, $width, $height, $color) {
  content: "";
  position: absolute;
  left: 0;
  bottom: $bottom;
  width: $width;
  height: $height;
  background-color: $color;
}

@mixin wrapper {
  margin: 0 auto;
  max-width: $desktop-width;
  padding: 0 15px;
  // padding: 0 calc(50% - 590px);
  // outline: 1px solid red;

  // @media (max-width: $tablet-width) {
  //   padding: 0;
  // }

  @media (max-width: $mobile-width) {
    min-width: $min-width;
    box-sizing: border-box;
  }
}

